import Zkrotor from './zkrotor/index'
import ZkLigature from './zkligature/index'
import ZKScratch from './zkscratch/index'
import zkAplayer from './zkaplayer/index'

// import Aplayer from 'vue-aplayer'
// const Aplayers = {
//   name: 'aplayer',
//   component: Aplayer
// }
// 
const components = [Zkrotor, ZkLigature, ZKScratch, zkAplayer]

//  install 
const install:any = function(Vue:any) {
  if (install.installed) return
  install.installed = true
  // 
  components.map(component => {
    Vue.component(component.name, component.component)
  })
}
const w:any = window
if (typeof window !== 'undefined' && w.Vue) {
  install(w.Vue)
}

export default {
  //  install 
  install,
  // 
  ...components
}
